import { useLoaderData } from "@remix-run/react";

export default function Results() {
  const url = "https://rpc.ankr.com/multichain";

  const body = {
    jsonrpc: "2.0",
    method: "ankr_getNFTsByOwner",
    params: {
      walletAddress: "0x186Ea56F0a40c5593A697B3E804968b8C5920Ff3",
      pageSize: 15,
      pageToken: "",
    },
    id: 1,
  };

  const [data, setData] = useState(null);

  async function fetchFromAnkr() {
    const data = await fetch(url, {
      method: "POST",
      body: JSON.stringify(body),
    });
    const json = await data.json();
    console.log("json: ", json);
    setData(json);
    return json;
  }

  //   const walletData = await fetchFromAnkr();
  //   fetchFromAnkr();

  return (
    <div>
      <button onClick={fetchFromAnkr}>test</button>
      <h1>gm</h1>
      <h1>{data ? data?.result?.assets[0]?.imageUrl : "no data"}</h1>
    </div>
  );
}
